// Componete para exibir card de logs de notificações das movimentações do estoque
"use client"

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { useState, useEffect } from "react"
import apiClient from "@/lib/api-client"

interface NotifyLogsProps {
  setShowNotifyLogs: (value: boolean) => void
}

export function NotifyLogs({ setShowNotifyLogs }: NotifyLogsProps) {
  const [movements, setMovements] = useState<any>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchMovements = async () => {
      try {
        const { movements } = await apiClient.get("/movements")
        setMovements(movements)
      } catch (error) {
        console.error("Error fetching movements:", error)
      } finally {
        setLoading(false)
      }
    }

    fetchMovements()
  }, [])

  if (loading) return <div>Loading...</div>
  if (!movements) return <div>No data</div>

  return (
    <Dialog>
      <DialogTrigger asChild>
        <Button variant="outline">Ver detalhes</Button>
      </DialogTrigger>

      <DialogContent className="max-w-md">
        <DialogHeader>
          <DialogTitle>Movimentação de Estoque</DialogTitle>
        </DialogHeader>

        <Card>
          <CardContent className="space-y-2 pt-4">
            <p><strong>Tipo:</strong> {movements.movement_type}</p>
            <p><strong>Quantidade:</strong> {movements.quantity}</p>
            <p>
              <strong>Estoque:</strong>{" "}
              {movements.stock_before} → {movements.stock_after}
            </p>
            <p><strong>Data:</strong> {new Date(movements.movement_date).toLocaleString()}</p>
            {movements.notes && (
              <p><strong>Observação:</strong> {movements.notes}</p>
            )}
          </CardContent>
        </Card>
      </DialogContent>
    </Dialog>
  )
}
